<template>
    <div>
        <!-- 导航 -->
        <div class="data-menu">
            <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal"
                @select="handleSelect">
                <el-menu-item index="a">数据A</el-menu-item>
                <el-menu-item index="b">数据B</el-menu-item>
            </el-menu>
        </div>
        <!-- 数据部分 -->
        <div class="userdata-container">
            <el-table
                v-loading="loading"
                :data="applyList"
                style="width: 100%;margin-bottom: 20px;"
                >
                <el-table-column prop="id" width="60" align="center" label="序号"></el-table-column>
                <el-table-column prop="dataId" align="center" label="数据ID" width="100"></el-table-column>
                <el-table-column prop="data" align="center" label="数据名称"></el-table-column>
                <el-table-column prop="dataTime" align="center" label="申请时间"></el-table-column>
                <el-table-column prop="username" align="center" label="申请用户"></el-table-column>
                <el-table-column align="center" label="模型使用状态">
                <template slot-scope="{row}">
                        <el-tag :type="row.status | statusType">{{ row.status | statusVal }}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="操作" align="center">
                    <template slot-scope="scope">
                    <el-button size="mini" type="primary" @click="onEdit(scope.row)">审核</el-button>
                    <el-button size="mini" type="danger" @click="onDel(scope.row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <!-- 底部导航页 -->
        <div class="bottom-page">
            <Pagination
                :totalMsg="query.total"
                :pageSize="query.pageSize"
                :pageIndex="query.pageIndex"
                @setSize="getSize"
                @setIndex="getIndex"
            >
            </Pagination>
        </div>
        
    </div>
</template>

<script>

import Pagination from '@/components/Pagination.vue'

export default {
    components:{
        Pagination
    },
    filters:{
        statusType(type){
            const mapType={
                1:'success',
                0:'info'
            }
            return mapType[type];
        },
        statusVal(val){
            const mapVal={
                1:'审核通过',
                0:'待审核'
            }
            return mapVal[val];
        }
    },
    data(){
        return{
            activeIndex:'a',
            loading:false,
            dataList:[],
            applyList:[],
            query:{
                total:null,
                pageSize:5,
                pageIndex:1
            }
        }
    },
    methods:{
        handleSelect(data){//切换数据
            let that=this;
            that.activeIndex = data;
            that.getApplyData();
        },
        getApplyData(){
            let that = this;
            that.loading = true;
            this.$api.govuser({
                page: that.query.pageIndex,
                pageSize: that.query.pageSize
            }).then(res=>{
                if(res.err_code == 100){
                    let data = res.data;
                    if(that.activeIndex == 'a'){
                        let mydata = data.userA.AllUserApply;
                        that.applyList = mydata.list;
                        that.query.total = mydata.total;
                    }else if(that.activeIndex == 'b'){
                        let mydata = data.userB.AllUserApply;
                        that.applyList = mydata.list;
                        that.query.total = mydata.total;
                    }
                    that.loading=false;
                }else{
                    that.$message({
                        message:'数据获取失败',
                        type:'error'
                    })
                }
            })
        },
        getSize(size){//底部导航
            this.query.pageSize = size;
            this.getApplyData();
        },
        getIndex(index){
            this.query.pageIndex = index;
            this.getApplyData();
        },
        onEdit(info){//审核
            let that = this;
            this.$api.govcheck(
                `${this.activeIndex}`,
                `${info.id}`
            ).then(res=>{
                if(res.err_code === 100){
                    that.$message({
                        message:'审核成功',
                        type:'success'
                    });
                    that.getApplyData();
                }else{
                    that.$message({
                        message:'审核失败',
                        type:'error'
                    });
                }
            })
        },
        onDel(info){//删除
            let that = this;
            this.$api.govdelcheck(
            `${this.activeIndex}`,
            `${info.id}`).then(res=>{
                if(res.err_code === 100){
                    that.$message({
                        message:'删除成功',
                        type:'success'
                    });
                    that.getApplyData();
                }else{
                    that.$message({
                        message:'删除失败',
                        type:'error'
                    });
                }
            })
        }
    },
    created(){
        this.getApplyData();
    }
}
</script>

<style lang="scss" scoped>    
    .userdata-btn{
        margin: 10px 0px;
    }
    .userdata-container{
        margin: 10px 10px 20px;
    }
    .bottom-page{
        background: #fff;
        display: flex;
        justify-content: center;
    }
</style>